@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Press Start 2P", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 5rem;
  background-image: linear-gradient(
    0deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
}

button {
  font-family: "Special Elite", cursive;
  padding: 0.8rem 2rem;
  border: none;
  background-color: wheat;
  margin: 2rem;
  background-color: burlywood;
  border-radius: 3px;
}
h1,
p {
  margin: 2rem auto;
}

.circles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.circle {
  background-color: grey;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  color: white;

  cursor: url(/speed_game/static/media/cat_2.d77c420e.png), auto;
  box-shadow: 0 0 10px rgb(72, 72, 72);
}
.circle:active {
  box-shadow: inset 0 0 40px rgb(96, 96, 96);
}

.circle p {
  font-size: 2rem;
}

.active {
  box-shadow: 0 0 25px yellow;
  border: 25px solid yellow;
  background-image: url(/speed_game/static/media/mouse_cheese.f4bf0d93.svg);
  background-size: 100%;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(51, 51, 51, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  position: relative;
  top: 0;
  left: 0;
  height: 50%;
  width: 50%;
  background-color: rgb(243, 215, 215);
  padding: 2rem;
  box-shadow: 0 0 5px #333;
  margin: 0 auto;
}
.popup p {
  padding: 1rem 0;
}
.close_btn {
  padding-left: 80%;

  size: 80px;
}

.App {
  text-align: center;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  cursor: url("/speed_game/static/media/cat 2.8fc8a1d0.svg");
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

